import React, { FC, useEffect, useState } from 'react'
import { Typography } from 'antd'
import { useRequest } from 'ahooks'
import { useParams } from 'react-router-dom'
import { getComponentStatService } from '@/services/stat'
import { getComponentConfByType } from '@/components/QuestionComponents'
const { Title } = Typography

type PropsType = {
  selectedComponentId: string
  selectedComponentType: string
}

const StatCharts: FC<PropsType> = props => {
  const { selectedComponentId, selectedComponentType } = props
  const { id = '' } = useParams()
  const [stat, setStat] = useState([])
  const { run } = useRequest(
    async (questionId, componentId) => await getComponentStatService(questionId, componentId),
    {
      manual: true,
      onSuccess(res) {
        setStat(res.stat)
      },
    }
  )
  // ç›‘å¬é€‰ä¸­çš„ç»„ä»¶id å¯ä»¥åŠæ—¶åˆ‡æ¢å›¾è¡¨
  useEffect(() => {
    if (selectedComponentId) run(id, selectedComponentId)
  }, [id, selectedComponentId])

  // ç”Ÿæˆç»Ÿè®¡å›¾è¡¨
  function genStatElem() {
    if (!selectedComponentId) return <div>æœªé€‰ä¸­ç»„ä»¶</div>

    const { StatComponent } = getComponentConfByType(selectedComponentType) || {}

    if (!StatComponent) return <div>è¯¥ç»„ä»¶æ— ç»Ÿè®¡å›¾è¡¨</div>

    return <StatComponent stat={stat} />
  }

  return (
    <>
      <Title level={3}>å›¾è¡¨ç»Ÿè®¡</Title>
      <div>{genStatElem()}</div>
    </>
  )
}

export default StatCharts
